@import url(https://fonts.googleapis.com/css?family=Lato|Roboto:400,700,900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-family: "Lato", sans-serif;
}

code {
  font-family: "Lato", source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: red;
}

.App-header {
  background-color: #fafafa;
  min-height: 70vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(16px + 2vmin);
  color: #2b2c2d;
}

.App-education {
  background-color: #fafafa;
  min-height: 45vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(16px + 2vmin);
  color: #2b2c2d;
}

/* background: linear-gradient(to bottom, #fafafa 50%,#f2f2f2 50%); */

.App-about {
  background-color: whitesmoke;
  min-height: 60vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 18px;
  color: black;
}

.App-skills{
  background-color: white;
  min-height: 60vh;
  padding: 40px 0 40px 0;
}

.App-portfolio {
  background-color: white;
  min-height: 60vh;
  padding: 40px 0 40px 0;
}

.navbar {
  background: #fafafa;
}

/* Button */
.button {
  margin-top: 20px;
  border-radius: 15px;
  background-color: #fff;
  color: #2b2c2d;
}

.btn-space {
  margin-right: 40px;
}

.button.raised {
  -webkit-transition: box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
}

.button.raised:active {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

.strip {
  padding: 10px 20px 6px 20px;
}

.me {
  max-width: 45%; 
  height: auto;
  border: solid 5px;
  border-color: white;
  box-shadow: 0px 7px 29px -7px rgba(0,0,0,0.28);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
